@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;800&display=swap);
body {
  background-color: #1b2022;
  box-sizing: border-box;
}

.about__container {
  text-align: center;
  margin-top: 80px;
  margin-left: 300px;
  width: 70%;
  padding: 20px 10px;
  color: #f1f2f6;
  background-color: #2f3542;
  font-size: 2em;
  font-weight: 500;
  font-family: "Playfair Display", serif;
  box-shadow: 1px 19px 14px -7px rgba(0, 0, 0, 0.39);
}

* {
  text-decoration: none;
}

.movie {
  background-color: #f5f6fa;
  font-family: "Playfair Display", serif;
}

.movie__img {
  width: 100%;
  transform: translate(-5%, -5%);
  box-shadow: 29px 33px 13px -22px rgba(0, 0, 0, 0.53);
}

.movie__title {
  color: #1e272e;
  padding: 0 10px;
  margin-top: -10px;
  font-size: 2em;
  font-weight: 800;
}

.movie__year {
  margin-top: -35px;
  margin-right: 15px;
  color: #57606f;
  text-align: right;
  font-size: 1.3em;
  font-weight: 400;
}

.summary__container {
  width: 100%;
  background-color: #f1f2f6;
  border-radius: 6px;
  padding: 6px 0;
}

.movie__summary {
  padding: 8px 12px;
  color: #1e272e;
  font-size: 1.2em;
  font-weight: 400;
}

.loader {
  color: white;
  margin-top: 100px;
  transform: translateX(48%);
  font-family: "Playfair Display", serif;
  font-size: 1.5em;
  font-weight: 400;
}

.movies {
  margin-top: 80px;
  margin-left: 180px;
  margin-right: 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  grid-row-gap: 80px;
  row-gap: 80px;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: repeat(1, 1fr);
    width: 40%;
    transform: translateX(40%);
  }
}

.nav {
  margin-left: 25px;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
}

.home {
  margin-bottom: 20px;
  color: #f1f2f6;
}

.about {
  color: #f1f2f6;
}

.detail {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 70%;
  background-color: white;
  margin-left: 250px;
  margin-right: 0px;
  margin-top: 80px;
  font-family: "Playfair Display", serif;
}

.detail__img {
  width: 90%;
  height: 90%;
  transform: translate(-5%, -5%);
  box-shadow: 29px 33px 13px -22px rgba(0, 0, 0, 0.53);
}

.detail__container {
  width: 95%;
}

.detail__title {
  font-size: 3em;
  font-weight: 800;
}

.detail__year {
  margin-top: -30px;
  margin-right: 30px;
  font-size: 1.3em;
  text-align: right;
  color: #57606f;
  font-weight: 400;
}

.detail__summary {
  padding: 8px 12px;
  margin-right: 8px;
  font-size: 1.5em;
  color: #1e272e;
  text-align: justify;
  word-break: keep-all;
  word-wrap: break-word;
  font-weight: 500;
}

.detail__genres {
  display: flex;
}

.detail__genre {
  margin-right: 25px;
  color: #57606f;
  font-weight: 400;
  font-size: 1.3em;
}

